















































import pdf from 'vue-pdf';
import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { baseUrl } from '@/main';

@Component({
  components: { pdf },
})
export default class Document extends Vue {
  // eslint-disable-next-line no-undef
  @Prop() initialPage!: number;

  @Prop() guidanceCategory!: string;

  @Prop() guidanceText!: string[];

  currentPage = 1

  inputBoxPage = this.currentPage;

  pageCount = 0

  publicPath = `${baseUrl}${process.env.BASE_URL}`

  filename = 'NCHRP_1-54_Guidelines_2019_v18.pdf';

  progress = 0;

  setProgress(event) {
    this.progress = event;
  }

  updateCurrentPage(page) {
    this.currentPage = Number(page);
    this.inputBoxPage = this.currentPage;
  }

  nextPage() {
    this.updateCurrentPage(this.currentPage += 1);
  }

  prevPage() {
    this.updateCurrentPage(this.currentPage -= 1);
  }

  beforeMount() {
    console.log('showing document');
    this.updateCurrentPage(this.initialPage);
  }
}
