





































import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';

@Component
export default class SetDesignFactors extends Vue {
  @Prop() project!: IDrainageProject;

  crossSlope = [
    '<1%',
    '1-3%',
    '>3%',
  ];

  crossSlopeOptions = this.crossSlope.map((x) => ({ text: x, value: x }))

  waterSourceTypes = [
    { designation: 'Surface infiltration' },
    { designation: 'Edge infiltration' },
    { designation: 'Capillary action and vapor movements' },
    { designation: 'High ground seepage' },
    { designation: 'Changing depth of water table' },
  ]

  sourceFields = [
    { key: 'designation', label: 'Water Sources\n(select all that apply)' },
  ]

  onRowSelect(items) {
    this.project.waterSources = items.map((x) => x.designation);
  }
}
