


































































































import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject, PavementTypes, ProjectTypes } from '@/lib/interfaces/IDrainageProject';

@Component
export default class Sidebar extends Vue {
  @Prop() project!: IDrainageProject;

  @Prop() resultsAreReady!: boolean;

  pavementTypes = PavementTypes;

  projectTypes = ProjectTypes;

  scrollIntoView(evt) {
    // Adapted from https://stackoverflow.com/questions/24665602/scrollintoview-scrolls-just-too-far
    // and the bootstrap-vue scrollspy instructions

    evt.preventDefault();
    const href = evt.target.getAttribute('href');
    const el = href ? document.querySelector(href) : null;
    if (el) {
      const y = el.getBoundingClientRect().top + window.pageYOffset - 70;

      window.scrollTo({ top: y, behavior: 'smooth' });
      // this.$refs.content.scrollTop = el.offsetTop;
    }
  }
}
