































































import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject, PavementTypes } from '@/lib/interfaces/IDrainageProject';
import {
  asphaltDistresses,
  compositeDistresses,
  distressImageFilenames,
  distressLinks,
  severityLevels,
} from '@/lib/distressTypes';

@Component
export default class SetDistresses extends Vue {
  @Prop() project!: IDrainageProject;

  distressImageFilenames = distressImageFilenames;

  distressLinks = distressLinks;

  pavementTypes = PavementTypes;

  radioOptions = Object.values(severityLevels).map((x) => ({ text: x, value: x }))

  get distressTypes() {
    if (this.project.pavementType === PavementTypes.asphalt) {
      return Object.keys(asphaltDistresses);
    }

    if (this.project.pavementType === PavementTypes.composite) {
      return Object.keys(compositeDistresses);
    }

    return [];
  }

  get distressEnum() {
    if (this.project.pavementType === PavementTypes.asphalt) {
      return asphaltDistresses;
    }

    if (this.project.pavementType === PavementTypes.composite) {
      return compositeDistresses;
    }

    return null;
  }
}
