import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';
import { ClimateTypes } from '@/lib/climateTypes';

const enum DrainageRequired {
  required = 'Recommended',
  feasible = 'Feasible',
  notRequired = 'Not Required'
}

const enum PermBins {
  low = '<10',
  med = '10-100',
  high = '>100'
}

const enum TrafficBins {
  low = '<2.5 million',
  med = '2.5 to 12 million',
  high = '> 12 million'
}

const table42 = {
  [ClimateTypes.wetFreeze]: {
    [TrafficBins.low]: {
      [PermBins.low]: DrainageRequired.feasible,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.med]: {
      [PermBins.low]: DrainageRequired.required,
      [PermBins.med]: DrainageRequired.required,
      [PermBins.high]: DrainageRequired.feasible,
    },
    [TrafficBins.high]: {
      [PermBins.low]: DrainageRequired.required,
      [PermBins.med]: DrainageRequired.required,
      [PermBins.high]: DrainageRequired.feasible,
    },
  },
  [ClimateTypes.wetNoFreeze]: {
    [TrafficBins.low]: {
      [PermBins.low]: DrainageRequired.feasible,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.med]: {
      [PermBins.low]: DrainageRequired.required,
      [PermBins.med]: DrainageRequired.feasible,
      [PermBins.high]: DrainageRequired.feasible,
    },
    [TrafficBins.high]: {
      [PermBins.low]: DrainageRequired.required,
      [PermBins.med]: DrainageRequired.required,
      [PermBins.high]: DrainageRequired.feasible,
    },
  },
  [ClimateTypes.dryFreeze]: {
    [TrafficBins.low]: {
      [PermBins.low]: DrainageRequired.notRequired,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.med]: {
      [PermBins.low]: DrainageRequired.feasible,
      [PermBins.med]: DrainageRequired.feasible,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.high]: {
      [PermBins.low]: DrainageRequired.feasible,
      [PermBins.med]: DrainageRequired.feasible,
      [PermBins.high]: DrainageRequired.notRequired,
    },
  },
  [ClimateTypes.dryNoFreeze]: {
    [TrafficBins.low]: {
      [PermBins.low]: DrainageRequired.notRequired,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.med]: {
      [PermBins.low]: DrainageRequired.notRequired,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
    [TrafficBins.high]: {
      [PermBins.low]: DrainageRequired.feasible,
      [PermBins.med]: DrainageRequired.notRequired,
      [PermBins.high]: DrainageRequired.notRequired,
    },
  },
};

export const subsurfaceDrainageNeeded = (project: IDrainageProject): DrainageRequired => {
  let trafficBin: TrafficBins = TrafficBins.low;
  if (project.twentyYearEsals < 2500000) {
    trafficBin = TrafficBins.low;
  } else if (project.twentyYearEsals < 12000000) {
    trafficBin = TrafficBins.med;
  } else if (project.twentyYearEsals >= 12000000) {
    trafficBin = TrafficBins.high;
  }

  let permBin: PermBins = PermBins.low;
  if (project.permeability_ft_per_day <= 10) {
    permBin = PermBins.low;
  } else if (project.permeability_ft_per_day <= 100) {
    permBin = PermBins.med;
  } else if (project.permeability_ft_per_day > 100) {
    permBin = PermBins.high;
  }

  return table42[project.climateZone][trafficBin][permBin];
};
