import { SoilType } from '@/lib/soilTypes';
import { severityLevels } from '@/lib/distressTypes';
import { ClimateTypes, IClimateEntry } from '@/lib/climateTypes';

export enum ProjectTypes {
  new = 'New',
  existing = 'Existing'
}

export enum PavementTypes {
  asphalt = 'Asphalt',
  composite = 'Composite',
  other = 'Other',
}

export interface IDrainageProject {
  projectType: ProjectTypes | null;
  name: string | null;
  location: string | null;
  description: string | null;

  pavementType: PavementTypes | null;
  subsurfaceType: SoilType | null;
  permeability_ft_per_day: number | null;

  // Both pavement type distresses
  stripping: severityLevels | null;
  rutting: severityLevels | null;
  potholes: severityLevels | null;
  crackLongitudinal: severityLevels | null;
  crackFatigue: severityLevels | null;
  crackTransverse: severityLevels | null;
  crackSlippage: severityLevels | null;

  // Asphalt only distresses
  bumpOrDistortion: severityLevels | null;
  corrOrRippling: severityLevels | null;
  depression: severityLevels | null;

  // Composite only distresses
  blowUp: severityLevels | null;
  pumpingErosion: severityLevels | null;
  faulting: severityLevels | null;
  curlingWarping: severityLevels | null;
  crackPCC: severityLevels | null;

  climateZone: ClimateTypes | null;

  // Traffic factors
  completionYear: number | null;
  twentyYearEsals: number | null;
  esalGrowthRate: number | null;
  initialTraffic: number | null;
  initialEsals: number | null;
  trafficGrowthRate: number | null;
  loadEquivalencyFactor: number | null;

  crossSlope: string | null;
  longitudinalSlope: number | null;

  waterSources: string[];

}
