export enum severityLevels {
  notPresent = 'Not Present',
  mild = 'Mild (<5%)',
  moderate = 'Moderate (5-15%)',
  severe = 'Severe (>15%)',
}

/*
 Asphalt
 */

export enum asphaltDistresses {
  bumpOrDistortion = 'Bump or distortion',
  corrOrRippling = 'Corrugation or rippling',
  stripping = 'Stripping',
  rutting = 'Rutting',
  depression = 'Depression',
  potholes = 'Potholes',
  crackLongitudinal = 'Longitudinal Cracking',
  crackFatigue = 'Fatigue Cracking',
  crackTransverse = 'Transverse Cracking',
  crackSlippage = 'Slippage Cracking'
}

export interface IAsphaltDistressEntry {
  type: string;
  manifestation: string;
  moistureProblem: string;
  climaticProblem: string;
  materialProblem: string;
  loadAssociated: boolean;
  beginsInSurfaceLayer: boolean;
  beginsInBaseLayer: boolean;
  beginsInSubgrade: boolean;
}

