Object.entries(asphaltDistresses).forEach((kv) => {
  const k = kv[0];
  const v = kv[1];

  if (!Object.keys(asphaltDistressDetails).includes(k)) {
    asphaltDistressDetails[k] = {
      type: (function () {
        if (k.startsWith('crack')) {
          return 'Cracking';
        }
        return 'Surface Deformation';
      }()),
      manifestation: `${v} default manifestation`,
      moistureProblem: `${v} default moisture problem`,
      climaticProblem: `${v} default climatic problem`,
      materialProblem: `${v} default material problem`,
      loadAssociated: false,
      beginsInSurfaceLayer: false,
      beginsInBaseLayer: false,
      beginsInSubgrade: false,
    } as IAsphaltDistressEntry;
  }
});

