



































import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';
import { SoilType, soilTypeTable } from '@/lib/soilTypes';
import NumberInput from '@/views/components/inputFields/NumberInput.vue';

@Component({
  components: { NumberInput },
})
export default class SetSubsurface extends Vue {
  @Prop() project!: IDrainageProject;

  soilTypes = Object.keys(SoilType);

  soilDetails = soilTypeTable;

  soilFields = [
    'selected',
    'designation',
    'category',
    'description',
    { key: 'drainageQuality', label: 'Drainage' },
    'strength',
  ];

  onRowSelected(items) {
    console.log(items);
    this.project.subsurfaceType = items[0].designation;
  }
}
