import { IDrainageProject, PavementTypes, ProjectTypes } from '@/lib/interfaces/IDrainageProject';
import { severityLevels } from '@/lib/distressTypes';
import { SoilType } from '@/lib/soilTypes';
import { ClimateTypes } from '@/lib/climateTypes';

function newProject(testing = false): IDrainageProject {
  if (testing) {
    return {
      projectType: ProjectTypes.existing,
      name: 'Test Project',
      location: 'Wisconsin',
      description: 'Test description text.',

      pavementType: PavementTypes.asphalt,
      subsurfaceType: SoilType.GP,
      permeability_ft_per_day: 100,

      // Both pavement type distresses
      stripping: severityLevels.mild,
      rutting: severityLevels.moderate,
      potholes: severityLevels.notPresent,
      crackLongitudinal: severityLevels.notPresent,
      crackFatigue: severityLevels.notPresent,
      crackTransverse: severityLevels.moderate,
      crackSlippage: severityLevels.notPresent,

      // Asphalt only distresses
      bumpOrDistortion: severityLevels.mild,
      corrOrRippling: severityLevels.notPresent,
      depression: severityLevels.severe,

      // Composite only distresses
      blowUp: severityLevels.notPresent,
      pumpingErosion: severityLevels.notPresent,
      faulting: severityLevels.notPresent,
      curlingWarping: severityLevels.notPresent,
      crackPCC: severityLevels.notPresent,

      climateZone: ClimateTypes.wetFreeze,
      twentyYearEsals: 30000000,

      completionYear: new Date(Date.now()).getFullYear(),
      initialTraffic: 100000,
      initialEsals: null,
      trafficGrowthRate: 0.1,
      loadEquivalencyFactor: 1.7,
      esalGrowthRate: 0.01,

      crossSlope: '1-3%',
      longitudinalSlope: 0,
      waterSources: [
        'Surface infiltration',
        'High ground seepage',
      ],
    };
  }

  return {
    projectType: null,
    name: null,
    location: null,
    description: null,

    pavementType: null,
    subsurfaceType: null,
    permeability_ft_per_day: null,

    // Both pavement type distresses
    stripping: severityLevels.notPresent,
    rutting: severityLevels.notPresent,
    potholes: severityLevels.notPresent,
    crackLongitudinal: severityLevels.notPresent,
    crackFatigue: severityLevels.notPresent,
    crackTransverse: severityLevels.notPresent,
    crackSlippage: severityLevels.notPresent,

    // Asphalt only distresses
    bumpOrDistortion: severityLevels.notPresent,
    corrOrRippling: severityLevels.notPresent,
    depression: severityLevels.notPresent,

    // Composite only distresses
    blowUp: severityLevels.notPresent,
    pumpingErosion: severityLevels.notPresent,
    faulting: severityLevels.notPresent,
    curlingWarping: severityLevels.notPresent,
    crackPCC: severityLevels.notPresent,

    climateZone: null,
    twentyYearEsals: null,

    completionYear: new Date(Date.now()).getFullYear(),
    initialTraffic: 100000,
    initialEsals: null,
    trafficGrowthRate: 0.1,
    loadEquivalencyFactor: 1.7,
    esalGrowthRate: 0.01,

    crossSlope: null,
    longitudinalSlope: 0,
    waterSources: [],
  };
}

export default newProject;
