






























import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';
import TrafficCalculator from '@/views/components/inputCategories/trafficCalculators/TrafficCalculator.vue';
import { soilTypeTable } from '@/lib/soilTypes';
import Document from '@/views/components/results/Document.vue';
import { subsurfaceDrainageNeeded } from '@/lib/table_4_2';

@Component({
  components: { Document, TrafficCalculator },
})
export default class ResultsInputsRecap extends Vue {
  @Prop() project!: IDrainageProject;

  documentPage = 1;

  guidanceCategory = '';

  guidancetext = '';

  setDocumentPage(guidance) {
    console.log('Setdocumetnpage');
    this.documentPage = guidance.page;
    this.guidanceCategory = guidance.label;
    this.guidancetext = guidance.values;
  }

  get showResults(): boolean {
    // return true;
    return this.project.projectType !== null
      && this.project.subsurfaceType !== null
      && this.project.climateZone !== null;
  }

  get subsurfaceDrainageNeedResult() {
    return subsurfaceDrainageNeeded(this.project);
  }

  resultInputFields = [
    { key: 'label', label: 'Category' },
    'values',
    'guidance',
  ];

  get resultInputElements(): { label: string; values: (string | number)[]; guidance: string; page: number }[] {
    const soilDetails = soilTypeTable.filter((s) => s.designation === this.project.subsurfaceType)[0];

    return [
      {
        label: 'Project Type',
        values: [
          `${this.project.projectType} pavement`,
        ],
        guidance: 'View Document',
        page: 1,
      },
      {
        label: 'Intervention Guidance',
        values: [
          `Zone Type: ${this.project.climateZone}`,
          `20-Year ESALs: ${this.project.twentyYearEsals.toLocaleString()}`,
          `Subgrade Permeability: ${soilDetails.drainageQuality}, ${this.project.permeability_ft_per_day} ft/day`,
          `<strong>Subdrainage features: ${this.subsurfaceDrainageNeedResult}</strong>`,
        ],
        guidance: 'See Table 4.2',
        page: 39,
      },
      {
        label: 'Subsurface Soil Type',
        values: [
          `Designation: ${this.project.subsurfaceType} -- ${soilDetails.category}`,
          `Description: ${soilDetails.description}`,
        ],
        guidance: 'See Table 3.1',
        page: 23,
      },
      {
        label: 'Subgrade Strength',
        values: [
          `${soilDetails.strength}`,
        ],
        guidance: 'See Table 3.1',
        page: 23,
      },

      {
        label: 'Subgrade Permeability',
        values: [
          soilDetails.drainageQuality,
          `${this.project.permeability_ft_per_day} ft/day`,
        ],
        guidance: 'See Table 3.1',
        page: 23,
      },
      {
        label: 'Pavement Type',
        values: [this.project.pavementType],
        guidance: 'See Tables 3.2, 3.3',
        page: 30,
      },
      {
        label: 'Design Factors',
        values: [
          `Cross Slope: ${this.project.crossSlope}`,
          `Longitudinal Slope: ${this.project.longitudinalSlope}%`,
        ],
        guidance: 'See Chapter 2',
        page: 17,
      },
      {
        label: 'Water Sources',
        values: this.project.waterSources,
        guidance: 'See Chapter 2',
        page: 17,
      },

    ];
  }
}
