







































































































































import { Component, Prop, Vue } from 'vue-property-decorator';
import CalculateLefModal from '@/views/components/inputCategories/trafficCalculators/CalculateLefModal.vue';
import CalculateFromAadtModal from '@/views/components/inputCategories/trafficCalculators/CalculateFromAadtModal.vue';
import NumberInput from '@/views/components/inputFields/NumberInput.vue';
import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';

enum EsalCalculationMethods {
  aadt = 'aadt',
  annual = 'annual',
  design = 'design'
}

@Component({
  components: {
    CalculateFromAadtModal,
    NumberInput,
    CalculateLefModal,
  },
})
export default class BaseTraffic extends Vue {
  @Prop() project!: IDrainageProject;

  @Prop() readonly validations!: any;

  get scenario() {
    return this.project;
  }

  initialTraffic = 0;

  trafficGrowthRate = 0.1;

  esals = 0;

  totalDesignEsals = 0;

  esalGrowthRate = 0.01;

  projectCompletionYear = new Date(Date.now()).getFullYear();

  loadEquivalencyFactor = 1.7;

  designPeriod_years = 20;

  $v = null;

  esalCalculatedFrom: EsalCalculationMethods = EsalCalculationMethods.aadt;

  esalMethods = EsalCalculationMethods;

  sync() {
    this.calculateTotalDesignEsals();
  }

  get esalMethodValidations() {
    // Validate based on method of calculation and whether or not it's porous (extra param)
    if (!this.$v) {
      return null;
    }

    if (this.esalCalculatedFrom === EsalCalculationMethods.aadt) {
      return this.$v.aadtGroup;
    }
    if (this.esalCalculatedFrom === EsalCalculationMethods.annual) {
      return this.$v.annualEsalGroup;
    }
    return this.$v.designEsalGroup;
  }

  // calculateEsals(){
  //   if (this.esalCalculatedFrom === EsalCalculationMethods.aadt){
  //     this.esals = 0
  //   }
  // }

  calculateCompletionYearEsals() {
    console.log('calculating esals aadt');
    this.project.initialEsals = Number((this.project.initialTraffic * this.project.loadEquivalencyFactor).toFixed(0));
    // this.calculateTotalDesignEsals();
  }

  calculateTotalDesignEsals() {
    console.log('calculating total esals');

    if (this.esalCalculatedFrom === EsalCalculationMethods.design) {
      return;
    }

    const { initialEsals } = this.project;

    let calcEsalGrowthRate = 0;

    if (this.esalCalculatedFrom === EsalCalculationMethods.aadt) {
      this.calculateCompletionYearEsals();
      calcEsalGrowthRate = (1 + this.trafficGrowthRate / 100) * (1 + this.esalGrowthRate / 100) - 1;
    } else {
      calcEsalGrowthRate = (1 + this.esalGrowthRate / 100) - 1;
    }

    if (calcEsalGrowthRate === 0) {
      this.totalDesignEsals = initialEsals * this.designPeriod_years;
    } else {
      // this.totalDesignEsals = initialEsals * ((Math.pow(1 + calcEsalGrowthRate, this.designPeriod_years)) - 1) / calcEsalGrowthRate;

      this.totalDesignEsals = (initialEsals * (((1 + calcEsalGrowthRate) ** (this.designPeriod_years)) - 1)) / calcEsalGrowthRate;
    }

    this.totalDesignEsals = Math.round(this.totalDesignEsals / 1000) * 1000;

    console.log("Setting total esals", this.totalDesignEsals);
    this.project.twentyYearEsals = this.totalDesignEsals;
    // this.totalDesignEsals = Math.round(totalDesignEsals);
  }

  esalCalculationMethods = EsalCalculationMethods;

  // setEsalMethod(method: string) {
  //   this.esalCalculatedFrom = method;
  //   if (method === EsalCalculationMethods.aadt) {
  //     this.esalCalculatedFrom = EsalCalculationMethods.aadt;
  //   } else if (method === EsalCalculationMethods.annual) {
  //     this.esalCalculatedFrom = EsalCalculationMethods.annual;
  //   } else if (method === EsalCalculationMethods.design) {
  //     this.esalCalculatedFrom = EsalCalculationMethods.design;
  //   } else {
  //     console.log('ESAL CALC METHOD NOT VALID');
  //     this.esalCalculatedFrom = EsalCalculationMethods.aadt;
  //   }
  // }

  mounted() {
    this.sync();
    // this.calculateTotalDesignEsals();
  }
}

