





























import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject, PavementTypes } from '@/lib/interfaces/IDrainageProject';
import TrafficCalculator
  from '@/views/components/inputCategories/trafficCalculators/TrafficCalculator.vue';
import {
  asphaltDistressDetails,
  asphaltDistresses,
  compositeDistressDetails,
  compositeDistresses,
} from '@/lib/distressTypes';

@Component({
  components: { TrafficCalculator },
})
export default class ResultsDistresses extends Vue {
  @Prop() project!: IDrainageProject;

  get distressTypes() {
    if (this.project.pavementType === PavementTypes.asphalt) {
      return Object.keys(asphaltDistresses);
    }

    if (this.project.pavementType === PavementTypes.composite) {
      return Object.keys(compositeDistresses);
    }

    return [];
  }

  get distressResults() {
    const distressTable = this.project.pavementType === PavementTypes.asphalt ? asphaltDistressDetails : compositeDistressDetails;

    return this.distressTypes.map((d) => {
      const details = distressTable[d];

      return {
        distress: this.project.pavementType === PavementTypes.asphalt ? asphaltDistresses[d] : compositeDistresses[d],
        level: this.project[d],
        moistureProblem: details.moistureProblem,
        climaticProblem: details.climaticProblem,
        materialProblem: details.materialProblem,
        loadAssociated: details.loadAssociated,
        defectStartsInAsphalt: details.beginsInSurfaceLayer,
        defectStartsInBase: details.beginsInBaseLayer,
        defectStartsInSubgrade: details.beginsInSubgrade,
      };
    }).filter((r) => r.level !== 'Not Present');
  }
}
