































import { Prop, Vue } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IDrainageProject } from '@/lib/interfaces/IDrainageProject';
import { climateDetails } from '@/lib/climateTypes';

@Component
export default class SetClimatic extends Vue {
  @Prop() project!: IDrainageProject;

  @Prop() id!: string;

  climateFields = [
    'selected',
    'designation',
    'precipitation',
    'freezeIndex',
  ];

  climateDetails = climateDetails;

  onRowSelected(items) {
    console.log(items);
    this.project.climateZone = items[0].designation;
  }
}
